.skills__container{
    grid-template-columns: repeat(3, 330px);
    column-gap: 2.8rem;
    justify-content: center;
}

h3{
    background: -webkit-linear-gradient(#fefefe, #f6f0aa);
    
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.skills__name:hover{
    font-weight: 800;
}

.skills__content{
    background: transparent;
    backdrop-filter: blur(3px);
    border: 1px solid rgba(0, 0, 0, 0, 1);
    border-radius: 20rem;
    padding: 2rem 4rem;
    border-radius: 1.25rem;
}

.skills__title{
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    text-align: center;
    margin-bottom: var(--mb-1-5);
}

.skills__box{
    display: flex;
    justify-content: center;
    column-gap: 2.5rem;
}

.skills__group{
    display: grid;
    align-items: flex-start;
    row-gap: 1rem;
}

.skils__data{
    display: flex;
    column-gap: 0.5rem;
}

.skills .bx-badge-check{
    font-size: 1rem;
    color: var(--title-color);
}

.skills__name{
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
    line-height: 18px;
}

.skills__level{
    font-size: var(--tiny-font-size);
}


/* =============BREAKPOINTS================ */

/* For large devices */
@media screen and (max-width: 992px) {
    .skills__container{
      
        grid-template-columns: max-content;
        row-gap: 2rem;
    }
  }
  
  /* For medium devices */
  @media screen and (max-width: 576x) {
    .skills__container{
        grid-template-columns: 1fr;
    }

    .skills__content{
        padding: 1.5rem;
    }
  }
  
  /*For small devices*/
  @media screen and (max-width: 350px) {
    .skills__container{
      display: grid;
      grid-template-columns: max-content;
      
      justify-content: center;
    }

    .skills__content{
      padding: 1.5rem;
      margin-left: 1.4rem;
      justify-content: center;
  }

    .skills__box{
        column-gap: 1.25rem;
    }

    .skills__name{
        font-size: var(--small-font-size);
    }
  }

