.about__container{
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    column-gap: 4rem;
}

.button{
    margin-top: 2rem;
    background:transparent;
    backdrop-filter: blur(20px);
    border: 0.3px solid white;
    border-radius: 2rem;
}

.about__img{
    width: 350px;
    border-radius: 1.5rem;
    justify-self: center;
    filter: brightness(1.15);
}

.about__info{
    grid-template-columns: repeat(3, 140px);
    gap: 0.5rem;
    margin-bottom: var(--mb-2);
}

.about__box{
    backdrop-filter: blur(10px);
    border: 1px solid rgb(75, 66, 66);
    border-radius: 0.75rem !important;
    text-align: center;
    padding: 1rem 1rem;
    font-size: small;
    background-image: linear-gradient(3deg, #41585D -2%, #edd30e 46%, #c02c2c 100%);
    -webkit-background-clip: text;
    color: transparent;
}

.about__icon{
    font-size: 1.5rem;
    color: var(--title);
    margin-bottom: var(--mb-0-5);
}

.about__titles{
    font-size: var(--small-font-size);
    font-weight: var(--font-medium);
    color: green;
}

.about__subtitle{
    font-size: var(--tiny-font-size);
}

.about__description{
    padding: 0 4rem 0 0;
    color: rgb(36, 123, 198);
    background-image: linear-gradient(63deg, #41585D -2%, #78e082 46%, #efaa28 100%);
    -webkit-background-clip: text;
    color: transparent;
}

.section__title{
    color: azure;
    margin-bottom: -0.5rem;
}

/* =============BREAKPOINTS================ */

/* For large devices */
@media screen and (max-width: 992px) {
        .about__container{
            grid-template-columns: 1fr;
            row-gap: 2.5rem;
        }

        .about__img{
            width: 220px;
        }

        .about__box{
            padding: 0.75 rem 0.5rem;
        }

        .about__data{
            text-align: center;
        }

        .about__info{
            justify-content: center;
        }

        .about__description{
            padding: 0.5rem;
            margin-bottom: 2rem;
        }
  }
  
  /* For medium devices */
  @media screen and (max-width: 576x) {
    .about__info{
        grid-template-columns: repeat(3, 1fr);
    }

    .about__description{
        padding: 0;
    }
  }
  
  /*For small devices*/
  @media screen and (max-width: 350px) {
    .about__info{
        grid-template-columns: repeat(2, 1fr);
        
    }
  }