@import url('https://fonts.googleapis.com/css2?family=Cantarell:ital@1&family=Caveat:wght@700&family=Great+Vibes&family=Open+Sans&family=Poppins:wght@400;500;600&display=swap');

.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    background: rgba(1, 1, 45, 0.3);
    backdrop-filter: blur(8px);
    z-index: 3;
}

.nav {
    height: calc(var(--header-height) + 1rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
}

.nav__link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font: var(--small-font-size);
    color: rgb(199, 196, 13);
    font-weight: var(--font-medium);
    transition: 0.3s;

}

.header__img {

    height: 40px;
    width: 40px;
    margin-top: 0.3rem;
    align-items: center;

}

.nav__logo {
    display: flex;
    color: rgb(235, 235, 226);
    font-size: xx-large;
    color: rgb(234, 102, 14);
    font-family: 'Cantarell', sans-serif;

}

.nav__toggle {
    color: var(--title-color);
    font-weight: var(--font-medium);
}

.nav__list {
    display: flex;
    column-gap: 2rem;
}

.nav__icon,
.nav__close,
.nav__toggle {
    display: none;

}

/*Active link*/


.nav__link:hover {
    color: rgb(177, 4, 79);
}

/* =============BREAKPOINTS================ */

/* For medium devices */
@media screen and (max-width: 768px) {
    .header {
        top: initial;
        bottom: 0;
    }

    .nav {
        height: var(--header-height);
    }

    .nav__menu {
        position: fixed;
        bottom: -900%;
        left: 0;
        width: 100%;
        background: rgba(30, 1, 1, 0.99);
        backdrop-filter: blur(6px);
        padding: 1rem 0.25rem 1rem;
        box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: 0.3s;
    }

    .show-menu {
        bottom: 0;
    }

    .nav__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }

    .nav__icon {
        font-size: 1.2rem;
    }

    .nav__close {
        position: absolute;
        right: 1.0rem;
        bottom: 7.5rem;
        font-size: 1.5rem;
        cursor: pointer;
        color: brown;
    }

    .nav__close:hover {
        color: var(--title-color-dark);
    }

    .nav__toggle {
        font-size: 1.1rem;
        cursor: pointer;
    }

    .nav__icon,
    .nav__close,
    .nav__toggle {
        display: block;
    }

    #apps {
        color: beige;
    }
}

/*For small devices*/
@media screen and (max-width: 350px) {
    .nav__menu {
        padding: 2rem 0.25rem 2rem;
    }

    .nav__list {
        column-gap: 0;
    }

    #apps {
        color: beige;
    }

    .nav__close {
        right: 1.0rem;
        bottom: 9.5rem;
    }
}