.footer {
    background: transparent;
    backdrop-filter: blur(2px);
    border-top: 1px solid white;
    height: -100px;
}

.footer__container {
    padding: -1rem 0 6rem;
}

.footer__title,
.footer__link {
    color: burlywood;

}

.footer__title {
    color: burlywood;
    text-align: center;
    margin-bottom: var(--mb-2);
}

.footer__link:hover {
    color: var(--title-color-dark);
}

.footer__list {
    display: flex;
    justify-content: center;
    column-gap: 1.5rem;
    margin-bottom: var(--mb-2);

}

.footer__social {
    display: flex;
    justify-content: center;
    column-gap: 1.25rem;
}

.footer__social-link {
    background-color: var(--title-color);
    color: var(--container-color);
    font-size: 1.125rem;
    padding: 0.4rem;
    border-radius: 0.5rem;
    display: inline-flex;
}

.footer__social-link:hover {
    background-color: var(--title-color-dark);
}

.footer__copy {
    display: block;
    margin-top: 4.5rem;
    color: orange;
    justify-content: center;
    text-align: center;
    font-size: var(--small-font-size);
}

/* =============BREAKPOINTS================ */

/* For large devices */
@media screen and (max-width: 992px) {
    .footer__social-link {
        padding: 0.25rem;
        border-radius: 0.25rem;
        font-size: 1rem;
    }
}

#github {
    font-size: xx-large;
}