.contact__container {
    grid-template-columns: repeat(2, max-content);
    justify-content: center;
    column-gap: 6rem;
    padding-bottom: 3rem;
}

.contact__title {
    text-align: center;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);
}

.contact__info {
    display: grid;
    row-gap: 1rem;
    grid-template-columns: 300px;
}

.contact__card {
    background: transparent;
    backdrop-filter: blur(2px);
    border: 2px solid rgba(224, 199, 88, 0.1);
    padding: 1rem;
    border-radius: 0.75rem;
    text-align: center;
    color: white;
}

.contact__card-icon {
    font-size: 2rem;
    color: var(title-color);
    margin-bottom: var(--mb-0-25);
    color: #006b32;
    
}

.contact__card-title,
.contact__card-description {
    font-size: var(--small-font-size);
}

.contact__card-title {
    font-weight: var(--font-medium);
}

.contact__card-data {
    display: block;
    margin-bottom: var(--mb-0-75);
}

.contact__button {
    color: hsl(0deg 78.33% 41.8%);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    column-gap: 0.25rem;
}

.contact__button-icon {
    font-size: 1rem;
    transition: 0.3s;
}

.contact__button:hover .contact__button-icon {
    transform: translate(0.25rem);
}

.contact__form {
    width: 360px;
}

.contact__info {
    text-align: center;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);
}

.contact__form-div {
    position: relative;
    margin-bottom: var(--mb-2);
    height: 4rem;
}

.contact__form-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid rgba(221, 169, 169, 0.3);
    background-color: transparent;
    backdrop-filter: blur(20px);
    /* color: var(--text-color); */
    color: chocolate;
    outline: none;
    border-radius: 0.75rem;
    padding: 1.5rem;
    z-index: 1;
}

.contact__form-tag {
    position: absolute;
    top: -0.75rem;
    left: 1.25rem;
    font-size: var(--smaller-font-size);
    padding: 0.25rem;
    background-color: var(--body-color);
    z-index: 10;
}

.contact__form-area {
    height: 11rem;
}

.contact__form-area textarea {
    resize: none;
}

/* =============BREAKPOINTS================ */

/* For large devices */
@media screen and (max-width: 992px) {
    .contact__container {
        column-gap: 3rem;
    }
}

/* For medium devices */
@media screen and (max-width: 768px) {
    .contact__container {
        grid-template-columns: 1fr;
        row-gap: 3rem;
    }

    .contact__info {
        justify-content: center;
    }

    .contact__form {
        margin: 0 auto;
    }
}

@media screen and (max-width: 576x) {
    .contact__info {
        grid-template-columns: 1fr;
    }

    .contact__form {
        width: 100%;
    }
}

/*For small devices*/
@media screen and (max-width: 350px) {
    .button--flex{
        margin-left: 5.9rem;
    }
}

h2 {
    display: flex;
    font-size: xxx-large;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;

    background-image: linear-gradient(63deg, #41585D -200%, #760674 46%, #e3e282 100%);
    -webkit-background-clip: text;
    color: transparent;
}

#linked{
    color: rgb(36, 137, 232);
    font-size: xx-large;
}

#github{
    color: rgb(255, 255, 255);
    font-size: xx-large;
    filter: brightness(1.7);
}