.qualification__container {
    max-width: 768px;
}

.section__titles{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: xx-large;
    font-weight: 700;
    color: aliceblue;

    background-image: linear-gradient(63deg, #41585D -200%, #5b0f0f 46%, #efff97 100%);
    -webkit-background-clip: text;
    color: transparent;
    text-align: center;
}

.section__subtitles{
    margin-top: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: large;
    color: aliceblue;
}

.qualification__tabs{
    display: flex;
    justify-content: center;
    margin-bottom: var(--mb-2);
    gap: 4rem;
}

.qualification__button{
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    color: var(--title-medium);
    margin: 0 var(--mb-1);
    cursor: pointer;
}

.qualification__button:hover{
    color: var(--title-color-dark);
}

.qualification__icon{
    font-size: 1.8rem;
    margin-right: var(--mb-0-25);
}

.qualification__active{
    color: white;
    font-weight: 200;
    font-size: large;
}

.qualification__sections{
    display: grid;
    grid-template-columns: 0.5fr;
    justify-content: center;
}

.qualification__content{
    display: none;
}

.qualification__content-active{
    display: block;
}

.qualification__data{
    display: grid;
    grid-template-columns: 1fr max-content 1fr;
    column-gap: 1.5rem;
}

.qualification__title{
    font-size: var(--normal-font-size);
    font-weight: var(--font-medium);
}
.qualification__subtitle{
    display: inline-block;
    font-size: var(--small-font-size);
    margin-bottom: var(--mb-1);
}

.qualification__calendar{
    font-size: var(--small-font-size);
}

.qualification__rounder{
    display: inline-block;
    width: 13px;
    height: 13px;
    background-color: var(--text-color);
    border-radius: 50%;
    
}

.qualification__line{
    position: static;
    display: block;
    height: 100%;
    width: 1px;
    background-color: var(--text-color);
    transform: translate(6px , -7px);
}

#merc{
    height: 20px;
    max-width: 300%;
    position: relative;
    top: -2px;
    left: -12px;
    filter: brightness(1.7);
}

#venus{
    height: 22.5px;
    max-width: 250%;
    position: relative;
    top: -2px;
    left: -5px;
    filter: brightness(1.5);
}

#earth{
    height: 30px;
    max-width: 250%;
    position: relative;
    top: -2px;
    left: -7px;
    filter: brightness(1.5);
}

#mars{
    height: 31px;
    max-width: 500%;
    position: relative;
    top: -2px;
    left: -17.4px;
    filter: brightness(1.5);
}

@media screen and (max-width: 600x) {
    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }
  }

.transpy-text {
    color: transparent;
}
.transpy-text h3 {
    background: transparent;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}