@import url('https://fonts.googleapis.com/css2?family=Cantarell:ital@1&family=Caveat:wght@700&family=Great+Vibes&family=Open+Sans&family=Poppins:wght@400;500;600&display=swap');

section{
    background-color: transparent;
}

.home__content {
    grid-template-columns: 116px repeat(2, 1fr);
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}

.home__social {
    display: grid; 
    grid-template-columns: max-content;
    row-gap: 1rem;
}

.home-social-icon{
    font-size: 1rem;
    color: var(--title-color);
}

.home-social-icon:hover{
    color: var(--title-color);
}

.home__description{
    background-image: linear-gradient(63deg, #c3da15 -10%, #db7575 46%, #8908a0 67%);
    -webkit-background-clip: text;
    color: transparent;
}

.home__title{
    font-size:xxx-large;
    font-family: 'Cantarell', sans-serif;
    margin-bottom: 0.5rem;

    background-image: linear-gradient(60deg, #5a15da -10%, #bc3030 46%, #9614aa 67%);
    -webkit-background-clip: text;
    color: transparent;
}


.home__hand{
    width: 38px;
    height: 38px;
    margin-left: 0.4rem;
}

.home__subtitle{
    position: relative;
    font-size: var(--h3-font-size) ;
    padding-left: 5.4rem;
    font-weight: var(--font-normal) ;
}

.home__subtitle::before{
    content: '';
    position: absolute;
    width: 70px;
    height: 1px;
    background-color: var(--title-color);
    left: 0;
    top: 1rem;
}

.home__description{
    max-width: 400px;
    margin-bottom: var(--mb-3);
}

.home__img {
    background-image: url(../../../assets/Sumit.jpg); 
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    text-shadow: 0 2px 5px yellow;
    order: 1;
    justify-self: center;
    border-image-slice: 1;
    /* width: 300px; 
    height: 300px; */
    margin-top: -5rem;
    position: relative;
    width: 100%;
    height: 100%;
    max-width: 312px;
    max-height: 380px;
    border-radius: 50%;
    border: 2px solid rgb(53 22 105);
}

.home__img::before{
    content: '';
    position: absolute;
    width: 150px;
    height: 40%;
    border: linear-gradient(blue, pink);
    animation: 4s linear infinite;
}

@keyframes animate{
    0%{
        transform: rotate(0deg);
    }

    100%{
        transform: rotate(360deg);
    }
} 



/* For large devices */
@media screen and (max-width: 992px) {
    .home__content{
        grid-template-columns: 100px repeat(2,fr);
        column-gap: 1.25rem;
        }
        
        .home__hand {
        width: 26px;
        height: 26px;
        } 
        .home__subtitle{
        padding-left: 3.75rem;
        margin-bottom: var(--mb-1);
        }
        
        .home__subtitle::before{
        width:42px;
        top:0.8rem;
        }

        .home__description{
            max-width: initial;
            margin-bottom: var(--mb-2-5);
        }

        .home__img{
            width: 250px;
            height: 250px;
        }
        
}

/* For medium devices */
@media screen and (max-width: 768px) {
    .home__content{
        grid-template-columns: 0.5fr 3fr;
        padding-top: 3.5rem;
    }

    .home__img{
        order: initial;
        justify-self: initial;
    }

    .home__data{
        grid-column: 1/3;
    }

    .home__img{
       
        width: 200px;
        height: 200px;
    }


}


/*For small devices*/
@media screen and (max-width: 350px) {
    .home__img{
        width: 180px;
        height: 180px;
    }

    .home__hand{
        width: 22px;
        height: 22px;
        
    }
}

.uil-github-alt{
    font-size: 2rem;
}

.uil-instagram{
    font-size: 2rem;
}
